import { Config } from './config';

export type Message = {
  origin: string;
  data: {
    state?: string;
    code?: string;
    error?: string;
    token?: string;
  };
};

export interface ISilentAuthService {
  setConfig(config: Config | null): Promise<void>;
  fetchAccessToken(): void;
  recieveMessage(event: Message): Promise<string>;
}
